export default {
	data() {
		return {
			loading: true
		};
	},

	methods: {
		open(message) {
			this.$refs["toast"].open(message);
		},

		openPosition(position) {
			this.$refs["toast"].open({
				position,
				message: "消息提示"
			});
		},

		openType(type) {
			this.$refs["toast"].open({
				type,
				message: "消息提示"
			});
		},

		openIcon(icon) {
			this.$refs["toast"].open({
				icon,
				position: "middle",
				message: "消息提示"
			});
		}
	}
};
